@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Montserrat:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto';
}
@-webkit-keyframes appear {
  0%{
    opacity: 0;
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
}
@keyframes appear {
  0%{
    opacity: 0;
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
}
